body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: white;
  padding: 18px 40px 18px 40px;
}

.header-title {
  text-align: left;
  font-size: 24px;
  font-weight: bold;
  color: black
}

.header-name {
  text-align: right;
  font-size: 24px;
  color: #4484ce;
}

.form {
  text-align: left;
  margin: auto;
  width: 40%;
  border: 1px solid #696969;
  border-radius: 3px;
}

.form-header {
  background-color: #4484ce;
  padding: 15px 0px 15px 30px;
  color: white;
  font-weight: bold;
  font-size: 20px;
  border-radius: 2px 2px 0px 0px;
}

.form-body {
  padding: 0px 30px 20px 30px;
  background-color: white;
  border-radius: 0px 0px 2px 2px;
}

.form-question {
  width: 100%;
  color: black;
  font-size: 18px;
  font-weight: bold;
  padding: 20px 0px 10px 0px;
}

.form-answer {
  width: 100%;
  text-align: left;
}

.answer-input {
  width: 40%;
  color: #696969;
  background-color: #f4f4f4;
  font-size: 16px;
  border: 1px solid #696969;
  border-radius: 3px;
  margin: 0;
  padding: 8px 8px 8px 8px;
}

.answer-input:focus {
  outline: 0;
}

.button-div {
  text-align: center;
}

.form-button {
  margin-top: 30px;
  padding: 8px 15px 8px 15px;
  font-size: 16px;
  color: white;
  background-color: #4484ce;
  border: none;
  border-radius: 3px;
  height: 34px;
  width: 120px;
}

.form-button:focus {
  box-shadow: 0 0 1pt 1pt #696969;
  outline: 0;
}

.form-button:hover {
  cursor: pointer;
}

.genderForm {
    display: flex;
    justify-content: space-around;
}

.activeForm {
    display: flex;
    justify-content: space-around;
}

.meal {
  background-color: #f4f4f4;
  color: #696969;
  border-radius: 3px;
  padding: 10px 10px 10px 10px;
  margin: 0px 0px 15px 0px;
  font-size: 16px;
  display: grid;
  grid-template-columns: 3fr 7fr 6fr 6fr 2fr;
}

.rec {
  background-color: #f4f4f4;
  color: #696969;
  border-radius: 3px;
  padding: 10px 10px 10px 10px;
  margin: 0px 0px 15px 0px;
  font-size: 16px;
  display: grid;
  grid-template-columns: 3fr 7fr 6fr 6fr 2fr;
}

.link-div {
  height: 20px;
  width: 40px;
}

.link-form {
  background-color: #696969;
  color: white;
  border: 1px solid black;
  border-radius: 3px;
  font-size: 10px;
  height: 25px;
  padding: 0;
  width: 50px;
}

.link-form:hover {
  cursor: pointer;
}

.link-form:focus {
  outline: 0;
}

.remove-button {
  background-color: #e85a4f;
  color: white;
  border: 1px solid black;
  border-radius: 3px;
  font-size: 10px;
  /* font-weight: bold; */
  padding: 0;
  height: 25px;
  width: 50px;
}

.remove-button:hover {
  cursor: pointer;
}

.remove-button:focus {
  outline: 0;
}

.meal-pic {
  height: 60px;
  width: 60px;
  border: 1px solid #696969;
  border-radius: 3px;
}

.meal-title {
  font-size: 18px;
  font-weight: bold;
}

.meal-details {
  font-size: 15px;
  display: grid;
  grid-template-rows: 1fr 1fr;
}

.hidden {
  visibility: hidden;
  width: 1px;
}

.daily-view {
  display: grid;
  text-align: left;
  grid-template-columns: 3fr 2fr;
  grid-gap: 40px;
  margin: 30px 40px 0px 40px;
  height: 75vh;
}

.daily-meals {
  background-color: white;
  border: 1px solid #696969;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
}

.meals-header {
  background-color: #4484ce;
  padding: 15px 30px 15px 30px;
  color: white;
  font-weight: bold;
  font-size: 22px;
  border-radius: 2px 2px 0px 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.meals-body {
  overflow: scroll;
  overflow-x: hidden;
  height: 420px;
}

.meals-list {
  margin: 20px 30px 20px 30px;
}

.list-header {
  color: #696969;
  font-size: 20px;
  font-weight: bold;
  padding-bottom: 15px;
  display: flex;
  justify-content: space-between;
}

.add-meal-button {
  background-color: #4484ce;
  color: white;
  border-radius: 3px;
  border: none;
  font-size: 16px;
  padding: 3px 6px 3px 6px;
  height: 24px;
  width: 60px;
}

.add-meal-button:focus {
  box-shadow: 0 0 1pt 1pt #696969;
  outline: 0;
}

.add-meal-button:hover {
  cursor: pointer;
}

.add-meal-label:hover {
  cursor: pointer;
}

.meals-rec {
  margin: 20px 30px 0px 30px;
}

.rec-header {
  color: #696969;
  font-size: 20px;
  font-weight: bold;
  padding-bottom: 15px;
}

.daily-nutrition {
  background-color: white;
  border: 1px solid #696969;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
}

.nutrition-header {
  background-color: #4484ce;
  padding: 15px 0px 15px 30px;
  color: white;
  font-weight: bold;
  font-size: 22px;
  border-radius: 2px 2px 0px 0px;
}

.nutrition-body {
  margin: 20px 30px 0px 30px;
}

.daily-date {
  text-align: center;
  background-color: white;
  border: 1px solid #696969;
  border-radius: 3px;
  font-size: 16px;
  color: #696969;
  font-weight: bold;
  padding: 3px 0px 3px 0px;
}

.daily-date:focus {
  outline: 0;
}

.daily-date:hover {
  cursor: pointer;
}

.summary-table {
  width: 100%;
  font-size: 16px;
  color: #696969;
  border-collapse: separate;
}

.summary-loading {
  padding-top: 30px;
  text-align: center;
}

th {
  font-size: 18px;
}
tr.table-body-row > td {
  font-size: 16px;
  padding-top: 10px;
}

.App {
  text-align: center;
  background-color: #F4F4F4;
  height: 100vh;
  display: flex;
  flex-direction: column;  
  color: #4484ce;
}

.Start-title {
  font-size: 34px;
  color: #696969;
  padding: 35px 0px 35px 0px;
}
